<template lang="pug">
.group__scholar
    label.group__scholar-info {{index}}) {{scholar.lastName}} {{scholar.firstName}}
    input(@change='onChange()' v-model='selected' type='checkbox').group__scholar-content 
</template> 
<script>
export default {  
    data() {
        return {
            selected: false
        }
    },
    props: {
        visible: Boolean,
        scholar: Object,
        index: Number
    },
    methods: {
        onChange() {
            this.$emit('eventRemove', this.scholar.id)
        }
    }
}
</script>
